<template>
<div class="CustomerUser">
  <section class="content-header">
    <h1>
      YamiYami Users
      <small>Customers</small>
    </h1>
    <ol class="breadcrumb">
      <li><router-link to="/"><i class="fa fa-dashboard"></i> Home</router-link></li>
      <li class="active">YamiYami Users</li>
    </ol>
  </section>
  <notifications group="notify" class="notifications"></notifications>

  <!-- Main content -->
  <section class="content">
    <div class="box">
      <div class="box-body">

        <!-- Data Table -->
        <div class="">
          <VclTable v-if="isLoading"/>
          <DataTable v-else :has_data="datas.length>0">
            <template #header>
              <!-- <th>Select</th> -->
              <th class="id-col-user">ID</th>
              <th>Gender</th>
              <th>Name</th>
              <th>Email</th>
              <th>Registered</th>
              <th>Purchases</th>
              <th>Total Spent</th>
              <th>Status</th>
              <th class="option-column">Options</th>
            </template>
            <tr v-for="(data, index) in datas" :key="index">
              <!-- <td class="sorting_1"><input type="checkbox" name="check"></td> -->
              <td>
                <span v-if="userPrefix">
                  {{userPrefix}}{{ index + 1 }}
                </span>
                <span v-else>
                  {{ index + 1 }}
                </span>
                
              </td>
              <td><img :src="data.avater" height="35px"></td>
              <td>{{ data.first_name }} {{data.last_name}}</td>
              <td>{{ data.email }}</td>
              <td>{{ data.created_at }}</td>
              <td>{{ data.purchase }}</td>
              <td>{{ data.spent }}</td>
              <td>
                <a href="#" @click.prevent="" class="btn btn-success btn-table" v-if="data.status=='ACTIVE'">{{ data.status }}</a>
                <a href="#" @click.prevent="" class="btn btn-warning btn-table" v-if="data.status=='PENDING'">{{ data.status }}</a>
                <a href="#" @click.prevent="" class="btn btn-danger btn-table" v-if="data.status=='BLOCKED'">{{ data.status }}</a>
              </td>
              <td>
                <BsModal
                    type="green"
                  >
                  <template #button>
                    <span class="btn btn-info btn-table"><i class="fa fa-eye"></i></span>
                  </template>
                  <template #title>
                    {{ data.shopName }}
                  </template>
                  <div class="container-fluid">
                    <section class="content-header">
                      <h1>PartyService Profile</h1>
                      <ol class="breadcrumb">
                        <li><router-link to="/"><i class="fa fa-dashboard"></i> Home</router-link></li>
                        <li class="active">PartyService Profile</li>
                      </ol>
                    </section>
                    <br/>
                    <div class="row">
                      <div class="col-sm-3">
                        <ProfileBox></ProfileBox>
                        <AboutBox></AboutBox>
                      </div>
                      <div class="col-sm-9">
                        <ProfileTabs></ProfileTabs>
                      </div>
                    </div>
                  </div>
                </BsModal>
                <BsModal
                    smaller
                    type="white"
                    :height="70"
                  >
                  <template #button>
                    <span class="btn btn-success btn-table"><i class="fa fa-close"></i></span>
                  </template>
                  <template #title>
                    Shop Options
                  </template>
                  <div class="container-fluid text-center">
                    <a href="#" class="btn btn-warning" style="margin-right: 20px;padding: 6px 40px;">Suspend</a>
                    <a href="#" class="btn btn-danger" style="padding: 6px 40px;">Deactivate</a>
                  </div>
                </BsModal>
              </td>
            </tr>
          </DataTable>
        </div>

      </div>
    </div>
  </section>
</div>
</template>

<script>
import DataTable from '@/components/DataTable.vue'
import BsModal from '@/components/BsModal.vue'
import ProfileBox from '@/components/ProfileBox.vue'
import AboutBox from '@/components/AboutBox.vue'
import ProfileTabs from '@/components/ProfileTabs.vue'
import { mapState } from 'vuex'
import { VclFacebook, VclInstagram,VclTable } from 'vue-content-loading';


export default {
  name: 'CustomerUser',
  components: {
    DataTable, BsModal, ProfileBox, AboutBox, ProfileTabs, VclTable
  },
  computed : {
      ...mapState(['PREFIXES']),
      userPrefix(){
        if(Object.keys(this.PREFIXES).length > 0 && this.PREFIXES.user_id !== undefined){
          return this.PREFIXES.user_id;
        }
        return null;
      }
  },
  data(){
    return{
      isLoading : false,
      datas : []
    }
  },
  mounted(){
    this.getPrefixes();
    this.getUserListing();
  },
  methods:{
    getPrefixes : function(){
        var url = process.env.VUE_APP_API_BASE_URL+'prefix/list';
        this.$store.dispatch('HTTP_GET_REQUEST' ,  url)
        .then(response => {
            if(response.data.error == false && response.data.data)
            {   
              let data = Object.assign({}, response.data.data);
              this.$store.commit('SET_PREFIXES',data);
            }
        })
        .catch(error => {
          console.log(error)
        })
    },
    getUserListing : function(){
        var url = process.env.VUE_APP_API_BASE_URL+'users/list'
        this.isLoading = true;
        this.$store.dispatch('HTTP_GET_REQUEST' ,  url)
        .then(response => {
            this.isLoading = false;
            if(response.data.error == false)
            {
                this.datas = response.data.data;
            }
            else
            {
              this.$notify({
                  group: 'notify',
                  type: 'warn',
                  title: 'Failure',
                  text: response.data.message || 'something went wrong.'
                });
            }
        })
        .catch(error => {
            this.isLoading = false;
            let errormessage = error.response.data.message;
            this.$notify({
                group: 'notify',
                type: 'error',
                title: 'Failure',
                text: errormessage
            });
        })
      },
  }
}
</script>
<style>
.id-col-user
{
  width : 120px !important;
}
</style>